import Vuex from 'vuex'
import persistedState from 'vuex-persistedstate'

const store = new Vuex.Store({
  //vuex-persistedstatestate
  plugins: [
    persistedState({ storage: window.sessionStorage })
  ],

  // this.$store.state.***
  state: {
    windowSize: 0,
    windowHeight: 0,
  },

  mutations: {
    //state this.$store.commit('remover', 'key1');
    remover (state, key) {
      state[key] = '';
    },

    //state this.$store.commit('upDate', {key1: 'value3'});
    upDate (state, obj) {
      let key = Object.keys(obj)[0];
      state[key] = obj[key];
    },

    //state
      addUserInfo (state, obj){
          state.userInfo = obj;
      },
  },

});

export default store
